.ul-nav li {
    background: #6660607a;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: calc(5px + 2vmin);
}

.ul-nav li:hover {
    background: #666060ef;
    cursor: pointer;
    ;
}

.nav-wrap {
    width: 30%;
}

.badge {
    border-radius: 50%;
    background-color: #282c34;
    padding-left: 4px;
    padding-right: 4px;
}