.loader {
    width: 10vmin;
    height: 10vmin;
    border-radius: 50%;
    border: 10px solid white;
    border-bottom: 10px solid blue;
    animation: loader-spin infinite 2s linear;
}

@keyframes loader-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}